body {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", Helvetica, sans-serif;
  min-width: 320px;
  position: relative;
}
body p {
  margin: 0;
}
body h1,
body h2,
body h3,
body h4,
body h5 {
  margin: 0;
  padding: 0;
}
body a {
  text-decoration: none;
}
body ul {
  padding: 0;
  margin: 0;
}
body ul li {
  list-style: none;
  margin: 0;
}
body .call {
  z-index: 999;
  position: fixed;
  background: #008000;
  right: 55px;
  width: 55px;
  height: 55px;
  border-radius: 50%;
  bottom: 55px;
  box-shadow: 0 1px 10px rgba(0, 0, 0, 0.46);
}
@media (max-width: 571px) {
  body .call {
    right: 25px;
  }
}
