  width: 100%;
  height: 100%;
  line-height: 55px;
}
body .call a .fa {
  font-size: 20px;
  color: #fff;
}
body .container {
  max-width: 1040px;
  padding: 0 20px;
  margin: auto;
}
body .box {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: wrap;
  margin-bottom: 80px;
}
body .box .item {
  z-index: 2;
  text-align: center;
  margin-right: 60px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media (max-width: 571px) {
  body .box .item {
    margin-right: 0;
    width: 100%;
