  justify-content: center;
}
body .buttons a {
  text-align: center;
  background: transparent;
  border: 1px solid #FFDC4F;
  padding: 10px 0;
  margin-right: 10px;
  cursor: pointer;
  flex-basis: 200px;
  transition: background 0.3s;
  z-index: 3;
  font-size: 17px;
  color: #fff;
  font-weight: 700;
}
@media (max-width: 409px) {
  body .buttons a:first-child {
